import {LocationModel} from '../models/Location.model';

let ids: number = 0;


const buildLocations = (): LocationModel[] => {
    const l = (Heb: string, En: string, ETA: number = 30): LocationModel => {
        ids++;
        return {
            EnName: En,
            id: ids.toString(),
            name: Heb,
            ETA: ETA,
        }
    }
    return [l('', 'Karmiel'),
        l('', 'Rakefet'),
        l('', 'Haifa', 50),
        l('', 'Acre', 45),
        l('', 'Hevrati', 50),
        l('', 'misgav'),
        l('', 'Tzurit'),
        l(' ', 'MaromGalil'),
        l('', 'Merkaz'),
        l('', 'Yuvalim'),
        l('', 'Yaad'),
        l('', 'Sachnin'),
        l(' ', 'RailKarmiel', 25),
        l('', 'Other', 60),


    ]
}

export const locations: LocationModel[] = buildLocations();

